/*global chrome*/

let currData = []
let pinnedData = [];
var shareReg = /share/g;
var rawReg = /raw/g;
var today = new Date();

chrome.browserAction.setIcon({path:"img/icon-16.png"});

chrome.webNavigation.onCompleted.addListener(function() {
  chrome.browserAction.setIcon({path:"img/icon-16_updated.png"});

  var trackList = localStorage.getItem("noteTrackList");
  var pinnedList = localStorage.getItem("pinnedNoteList");
  
  if(!pinnedList) {
    localStorage.setItem("pinnedNoteList", JSON.stringify(pinnedData))
  }
  
  if(!trackList) {
    localStorage.setItem("noteTrackList", JSON.stringify(currData))
  }
    chrome.tabs.query({ currentWindow: true, active: true }, function (tabs) {
      pinnedData = JSON.parse(pinnedList);
      currData = JSON.parse(trackList);
      var currUrl = new URL(tabs[0].url).pathname.replace('/', '');
      if(
        !currData.some(noteUrl =>  noteUrl.url === currUrl ) && 
        !pinnedData.some(noteUrl => noteUrl.url === currUrl) &&
        !shareReg.test(tabs[0].url) && 
        !rawReg.test(tabs[0].url) && 
        !tabs[0].url.includes("view-source:") &&
        !tabs[0].url.includes("/share/") &&
        !tabs[0].url.includes("/raw/")
        ) {
          currData.unshift({
              url: currUrl,
              time: today.getHours() + ":" + today.getMinutes() + ":" + today.getSeconds()+" "+today.getDate() + "/" + (today.getMonth()+1)+"/"+today.getFullYear()
          });
          localStorage.setItem('noteTrackList', JSON.stringify(currData));
      } else if(currData.some(noteUrl => noteUrl.url === currUrl)){
        for(var i = currData.length - 1; i >= 0; i--) {
            if(currData[i].url === currUrl) {
              currData.splice(i, 1);
            }
        }
        currData.unshift({
          url: currUrl,
          time: today.getHours() + ":" + today.getMinutes() + ":" + today.getSeconds()+" "+today.getDate() + "/" + (today.getMonth()+1)+"/"+today.getFullYear()
        });
        localStorage.setItem('noteTrackList', JSON.stringify(currData));
      }
    });
}, {url: [{urlMatches : 'https://notevn.com/'}]});